	module.exports = {"data":{"markdownRemark":{"html":"<p>The <a href=\"https://www.google.com/search?q=football+squares\">Super Bowl Square</a> gambling game is all luck, right? Not according to our calculations.</p>\n<p>An analysis of all 2,312 NFL games since 2009 revealed guessing certain numbers each quarter can increase your chances of winning. Your best bets are 0, 3, and 7. The numbers 4 and 6 aren’t bad either.</p>\n<p>Here is the full interactive cheat sheet:</p>\n<iframe width=\"100%\" height=\"450\" src=\"https://cdn.rawgit.com/jonsadka/aecd147ef336291f31e4/raw/ea95c23ab456a9138862cb7db28227a6737290a1/index.html\" marginwidth=\"0\" marginheight=\"0\" scrolling=\"no\"></iframe>\n<p>*Data source: NFL Game Center (preseason, regular season, and playoffs)</p>\n<h3>Explanation</h3>\n<p>What does this all mean? Let’s review how to play.</p>\n<p>Football squares is a simple betting a game. To play, guess the last digit of each team’s score at the end of each quarter. It’s easy enough to play at the office, against your family or at a NFL Super Bowl party. But, make sure you do your research first.</p>\n<p>To come up with the winning numbers for each quarter, we looked at all NFL game data since 2009. The full results can be seen in the visualization above. The x-axis denotes the last dig\\it of the home team’s score while the y-axis denotes the last digit of the away team’s score.</p>\n<p>So when your friends ask you to Football Squares this weekend, put your poker face on. You got this.</p>\n<h3>Special thanks</h3>\n<p>Special thanks to <a href=\"https://twitter.com/benji_rosen\">Benji Rosen</a> for his help on the copy.</p>","frontmatter":{"date":"1454659200000","path":"/blog/choosing-the-best-numbers-in-nfl-football-squares-to-beat-your-friends","title":"Choosing the best numbers in nfl football squares to beat your friends"}}},"pathContext":{}}

